<template>
  <div class="allText">
    <header>
      <div class="Navleft">
        <img
          src="../../../static/车友圈-左上角关闭.svg"
          alt="arrow"
          @click="goBack"
        />
      </div>
      <Nav>车友圈</Nav>
      <div class="Navright" @click="goPublish()">
        <img src="../../../static/车友圈-发表文字.svg" />
      </div>
    </header>
    <el-divider class="headerDivider"></el-divider>
    <div class="middle">
      <div class="MiddleLeft">
        <el-avatar icon="el-icon-user-solid" :src="avatarUrl" :size="65">
        </el-avatar>
        <span
          ><b class="name">{{ username }}</b></span
        >
      </div>
      <div class="MiddleRight" @click="goChatList()">
        <img src="../../../static/车友圈-好友聊天.svg" alt="arrow" />
      </div>
    </div>
    <!-- <el-divider class="middleDivider"></el-divider> -->
    <div v-infinite-scroll="load" style="overflow: auto">
      <ul v-for="(item, i) in texts" :key="i" class="main">
        <li @click="more(item)">
          <Moment
            :text="item.description"
            :time="item.createTime.substring(5, 16)"
            :name="item.username"
            :avatarUrl="item.avatarUrl"
          ></Moment>
        </li>
      </ul>
      <div style="width: 100%; height: 400px" v-if="texts.length < 4"></div>
    </div>
  </div>
</template>
<script>
import Moment from "../../../components/Moment.vue";
export default {
  data() {
    return {
      texts: [],
      pageNum: 1,
      totalPage: 0,
      avatarUrl: "",
      username: "",
    };
  },
  mounted() {
    this.getAll();
  },
  methods: {
    getAll() {
      this.$axios
        .get("/text/byUser", {
          params: {
            pageNum: this.pageNum,
            pageSize: 4,
          },
        })
        .then((res) => {
          console.log(res);
          if (res.msg === "success") {
            this.texts.push(...res.byUser);
            this.totalPage = res.totalPage;
            this.avatarUrl = this.texts[0].avatarUrl;
            this.username = this.texts[0].username;
          }
        });
    },
    goPublish() {
      this.$router.push("/Publish");
    },
    goChatList() {
      this.$router.push("/chatList");
    },
    load() {
      if (this.pageNum >= this.totalPage) return;
      else {
        this.pageNum++;
        setTimeout(this.getAll(), 2000);
      }
    },
    more(item) {
      this.$router.push({
        path: "/Detail",
        query: {
          item: item,
        },
      });
    },
    goBack() {
      this.$router.go(-1);
    },
  },
  components: { Moment },
};
</script>
<style lang="less" scoped>
.allText {
  width: 100vw;
  height: 100vh;
  background-color: #f3f4f4;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
header {
  display: flex;
  width: 94%;
  justify-content: space-between;
  padding: 10px;
  padding-bottom: 0;
  line-height: 40px;
  font-size: 20px;
}
.Navleft {
  transform: translate(86%, 10%);
  img {
    width: 20px;
    height: 20px;
  }
}
.Navright {
  transform: translate(-100%, 13%);
  img {
    width: 25px;
    height: 25px;
  }
}
.headerDivider {
  margin: 10px;
}
.middle {
  box-sizing: border-box;
  width: 90%;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  .name {
    margin-left: 20px;
  }
}
.MiddleLeft {
  height: 80px;
  font-size: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.MiddleRight {
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    width: 45px;
    height: 45px;
  }
}
.middleDivider {
  margin: 10px;
}
.main {
  margin-top: 10px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
</style>